.book-now{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin: 6rem;

    .book-btn {
        background: linear-gradient(216deg, #0E75B4 0%, #0393CA 50%, #002F99 100%);
        color: #ffff;
        font-size: 20px;
        font-weight: 1000;
        padding: 15px 30px;
        border-radius: 10px;
        margin-top: 2rem;
        border: none;
    }
}



@media  screen and (max-width: 900px) {
    .book-header {
        display: flex;
        justify-content: center;
    }
  }